import * as React from "react";
import { DataGrid } from "@mui/x-data-grid";
import { randomInt, randomUserName } from "@mui/x-data-grid-generator";
import Box from "@mui/material/Box";
import Stack from "@mui/material/Stack";
import Button from "@mui/material/Button";


const fakeData = [
    {
        "name": "name1",
        "type": "type1",
        "price": 100,
        "localization": "Poznan",
        "availability": "on call"
    },
    {
        "name": "name2",
        "type": "type1",
        "price": 100,
        "localization": "Poznan",
        "availability": "on call"
    },
    {
        "name": "name3",
        "type": "type1",
        "price": 100,
        "localization": "Poznan",
        "availability": "on call"
    },
    {
        "name": "name4",
        "type": "type1",
        "price": 100,
        "localization": "Poznan",
        "availability": "on call"
    },
    {
        "name": "name5",
        "type": "type1",
        "price": 100,
        "localization": "Poznan",
        "availability": "on call"
    },
    {
        "name": "name6",
        "type": "type1",
        "price": 100,
        "localization": "Poznan",
        "availability": "on call"
    }
] 

const columns = [
  { field: "name" },
  { field: "type", width: 150 },
  { field: "price", width: 150, type: "number" },
  { field: "localization", width: 150 },
  { field: "availability", width: 150 },
];

let idCounter = 0;
const createRow = (row) => {
  idCounter += 1;
  return { id: idCounter, ...row };
};

export default function UpdateRowsProp() {
  const [rows, setRows] = React.useState(() => fakeData.map(row => createRow(row)));

  const handleUpdateRow = () => {
    setRows((prevRows) => {
      const rowToUpdateIndex = randomInt(0, rows.length - 1);

      return prevRows.map((row, index) =>
        index === rowToUpdateIndex
          ? { ...row, username: randomUserName() }
          : row
      );
    });
  };

  const handleUpdateAllRows = () => {
    setRows(rows.map((row) => ({ ...row, username: randomUserName() })));
  };

  const handleDeleteRow = () => {
    setRows((prevRows) => {
      const rowToDeleteIndex = randomInt(0, prevRows.length - 1);
      return [
        ...rows.slice(0, rowToDeleteIndex),
        ...rows.slice(rowToDeleteIndex + 1),
      ];
    });
  };

  const handleAddRow = () => {
    setRows((prevRows) => [...prevRows, createRow()]);
  };

  return (
    <div style={{ width: "100%" }}>
      {/* <Stack
        sx={{ width: "100%", mb: 1 }}
        direction="row"
        alignItems="flex-start"
        columnGap={1}
      >
        <Button size="small" onClick={handleUpdateRow}>
          Update a row
        </Button>
        <Button size="small" onClick={handleUpdateAllRows}>
          Update all rows
        </Button>
        <Button size="small" onClick={handleDeleteRow}>
          Delete a row
        </Button>
        <Button size="small" onClick={handleAddRow}>
          Add a row
        </Button>
      </Stack> */}
      <Box sx={{ height: 400, bgcolor: "background.paper" }}>
        <DataGrid hideFooter rows={rows} columns={columns} />
      </Box>
    </div>
  );
}
